.profile__avatar {
  width: 120px;
  height: 120px;

  border-radius: 50%;
  background-image: url(../../../images/loadAvatar.gif);
  background-size: cover;
  grid-row: 1/3;
}

@media screen and (max-width: 700px) {
  .profile__avatar {
    grid-row: 1;
    justify-self: center;
  }
}